
import $ from "jquery";

import "./import/modules";


import '../../node_modules/lightslider/dist/js/lightslider.js';

import '../../node_modules/lightgallery/dist/js/lightgallery-all.js';


$(document).ready(function() {
    let slider = jQuery(".slider_home");
    slider.lightSlider( 
        {
            item: 1,
            autoWidth: false,
            slideMove: 1, // slidemove will be 1 if loop is true
            slideMargin: 10,
     
            addClass: 'slider_home_wrapper',
            mode: "slide",
            useCSS: true,
            cssEasing: 'ease', //'cubic-bezier(0.25, 0, 0.25, 1)',//
            easing: 'linear', //'for jquery animation',////
     
            speed: 400, //ms'
            auto: false,
            loop: false,
            slideEndAnimation: true,
            pause: 2000,
     
            keyPress: false,
            controls: true,
            prevHtml: '<svg class="arrow"><use xlink:href="../img/sprites/sprite.svg#arrow_left"></use></svg>',
            nextHtml: '<svg class="arrow"><use xlink:href="../img/sprites/sprite.svg#arrow_right"></use></svg>',
     
            rtl:false,
            adaptiveHeight: true,
     
            vertical:false,
            verticalHeight:500,
            vThumbWidth:100,
     
            thumbItem:10,
            pager: false,
            gallery: false,
            galleryMargin: 5,
            thumbMargin: 5,
            currentPagerPosition: 'middle',
     
            enableTouch:true,
            enableDrag:true,
            freeMove:true,
            swipeThreshold: 40,
     
            responsive : [],
     
            onBeforeStart: function (el) {},
            onSliderLoad: function (el) {},
            onBeforeSlide: function (el) {},
            onAfterSlide: function (el) {},
            onBeforeNextSlide: function (el) {},
            onBeforePrevSlide: function (el) {}
        }
    );




    
});


$(document).ready(function() {
    let $imageGallery = $('#imageGallery');
    let imageGallery_responsive = [
        {
            breakpoint:769,
            settings: {
                thumbItem: 4,
                thumbMargin: 10
              }
        },
        {
            breakpoint: 577,
            settings: {
                thumbItem: 3
              }
        },
        {
            breakpoint: 426,
            settings: {
                thumbItem: 2
              }
        }
    ];

    let imageGallery_options = {    
        gallery:true,
        item:1,
        loop:true,
        thumbItem:9,
        slideMargin:0,
        enableDrag: false,
        currentPagerPosition:'left',
        addClass: 'imageGallery_wrapper',
        controls: false,
        thumbMargin: 10,
        prevHtml: '<svg class="arrow"><use xlink:href="../img/sprites/sprite.svg#arrow_left"></use></svg>',
        nextHtml: '<svg class="arrow"><use xlink:href="../img/sprites/sprite.svg#arrow_right"></use></svg>',
        responsive : imageGallery_responsive,
        onSliderLoad: function(el) {
            el.lightGallery({
                selector: '#imageGallery .lslide'
            });
        }        
    }

    $imageGallery.lightSlider(imageGallery_options); 


    $('#galleryControll__prev').click(function(){
        $imageGallery.goToPrevSlide();
    });


    
    $('#galleryControll__next').click(function(){
        $imageGallery.goToNextSlide();
    })


    // $('.imageGallery_wrapper .lSPager.lSGallery').wrap('<div class="container container_thumbs"> </div>');   

  });


  $(document).ready(function() {



    function tooLogic (event){
        let $this = $(this);

        

        let $name = $this.data("name") || 'Cтол 4';
        let $src = $this.data("img_src") ||  'img/source/hallLayout/table_4.jpg';
        let $toolip = $('.tooltip');



        let $toolip__title = $toolip.find('.tooltip__title');
        let $toolip__img = $toolip.find('.tooltip__img');


        if($toolip__title.html() == $name && $toolip.hasClass('tooltip_active')  ) {
            $toolip.removeClass('tooltip_active');
            return;

        }      

        if ($toolip.hasClass('tooltip_active')) {
            $toolip.removeClass('tooltip_active');
        } 

    
        setTimeout( () =>{
            
            if ($(window).width() >= 992 ) {
        
                let coordinates = {
                    'left': event.pageX - 40,
                    'top': event.pageY - 250
                };  

                $toolip.css(coordinates);
            }
            $toolip__title.text($name);         
            $toolip__img.attr('src', $src);
            $toolip.addClass('tooltip_active');

        } , 300 );
        
    }

    

    let $places = $('svg .red');

    if ($(window).width() < 992 ) { tooLogic($places[4]); }

    $places.click(tooLogic);

 

  })